body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.grid-list {
  margin: 0 auto;
}
.grid-item {
  cursor: pointer;
  width: 300px;
}
.grid-item img {
  width: 300px;
}
.lightbox {
  z-index: 9999;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
}
.lightbox-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: calc(100vh - 57px);
  background: #313131;
  background: rgba(0,0,0,.85);
}
.lightbox-wrapper img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 400px;
}
@media screen and (max-width: 500px){
  .lightbox-wrapper img {
    width: auto;
  }
}
.lightbox-nav {
  height: 57px;
  width: 100%;
  background: #383838;
  border-bottom: 1px solid #323232;
}
.iconClose, .iconNext, .iconPrev {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 25px;
  display: block;
}
.iconNext, .iconPrev {
  width: 15px;
  margin: -13px 0 0 -8px;
  background: url('./assets/image/icons.png');
}
.iconNext {
  background-position: -70px -3px;
}
.iconPrev {
   background-position: -40px -3px;
}
.iconClose {
  width: 25px;
  margin: -13px 0 0 -13px;
  background: url('./assets/image/icons.png') -2px -3px;
}
.lightbox-close, .lightbox-next, .lightbox-prev {
  float: right;
  width: 88px;
  height: 57px;
  position: relative;
  cursor: pointer;
}
.lightbox-close:hover, .lightbox-next:hover, .lightbox-prev:hover {
  background: black;
}


